.product {
  &__container {
    padding-bottom: 80px;
    display: flex;
    flex-wrap: wrap;
  }

  &__slider {
    position: relative;
    display: flex;
    max-width: 686px;
  }

  &__info {
    flex: 1 1 auto;
    padding-top: 22px;
    margin-left: 6px;
    padding-left: 20px;
  }
}

.slider-product {
  &__nav {
    position: absolute;
    top: 24px;
    left: 24px;
    z-index: 2;
  }

  &__block {
    overflow: hidden;
  }
}
.slider-nav {
  &__item {
    width: 70px;
    height: 91px;
    opacity: 0.3;
    &--active {
      opacity: 1;
    }
    &:not(:last-child) {
      margin-bottom: 7px;
    }
  }
}

.block-slider {
}
.swiper-slide {
  margin-right: 5px;

  &__img .image {
    display: block;
    width: 97%;
    height: 97%;
    object-fit: cover;
  }
}
.product-info {
  &__title {
    margin: 0;
    padding-bottom: 8px;
    color: var(--brand-black, #333);
    font-size: 18px;
    font-weight: 600;
    line-height: 24px;
  }

  &__article {
    display: block;
    padding-bottom: 18px;
    color: var(--brand-grey-dark, #828282);
    font-size: 12px;
    font-weight: 400;
    line-height: 16px;
    letter-spacing: 0.48px;
  }

  &__comment {
    display: flex;
    align-items: center;
    padding-bottom: 50px;
  }

  &__price {
    padding-bottom: 32px;
    max-width: 198px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }

  &__dropdown {
    padding-bottom: 12px;
  }

  &__btns {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    padding-bottom: 12px;
  }

  &__modal {
    width: 100%;
    text-align: start;
    padding-left: 22px;
    margin-left: -22px;
    padding-bottom: 29px;
    color: var(--brand-black, #333);
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: 0.56px;
    text-decoration-line: underline;
    border-bottom: 0.5px solid #c4c4c4;
  }
}
.comment {
  &__text {
    padding-right: 12px;
    color: var(--brand-black, #333);
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: 0.56px;
  }

  &__link {
    display: flex;
    align-items: center;
    padding-left: 4px;
    color: var(--brand-black, #333);
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: 0.56px;
    transition: 0.3s ease-in;
    &:hover {
      color: #000;
      transition: 0.3s ease-in;
      text-decoration: underline;
    }
  }
}
.rating {
  &__wrap {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
  }

  &__label {
    position: relative;
    cursor: pointer;
    &::before {
      content: url("../img/star.svg");
    }
    &:hover {
      content: url("../img/star-fill.svg");
    }
    &:hover ~ .rating__label:before {
      content: url("../img/star-fill.svg");
    }
  }

  &__item {
    position: absolute;
    width: 0;
    height: 0;
    opacity: 0;
    visibility: hidden;
    &:checked ~ .rating__label:before,
    &:checked + .rating__label:before {
      content: url("../img/star-fill.svg");
    }
  }
}
.prod-icon {
  display: inline-block;
  width: 16px;
  height: 16px;
  fill: none;
  stroke: var(--brand-black);
}
.price-product {
  &__actual {
    padding-right: 12px;
    color: var(--brand-black, #333);
    font-size: 24px;
    font-weight: 700;
    line-height: 24px;
    letter-spacing: 0.96px;
    text-transform: uppercase;
  }

  &__old {
    padding-right: 12px;
    color: var(--brand-grey-dark, #828282);
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: 0.56px;
    text-decoration: line-through;
  }

  &__action {
    margin-top: 12px;
    padding: 4px 8px;
    color: var(--brand-black, #333);
    font-size: 12px;
    font-weight: 400;
    line-height: 16px;
    letter-spacing: 0.48px;
    border: 1px solid var(--brand-black, #333);
  }

  &__discount {
    margin-top: 12px;
    margin-left: 4px;
    padding: 4px 8px;
    color: var(--brand-black, #333);
    font-size: 12px;
    font-weight: 400;
    line-height: 16px;
    letter-spacing: 0.48px;
    border: 1px solid var(--brand-black, #333);
  }
}
.dropdown {
  position: relative;
  &__button {
    position: relative;
    display: block;
    width: 315px;
    text-align: left;
    padding-top: 8px;
    padding-bottom: 8px;
    padding-left: 8px;
    padding-right: 28px;
    border: 1px solid var(--brand-black, #333);
    color: var(--brand-black, #333);
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: 0.56px;
    cursor: pointer;
    &:focus,
    .dropdown__button--active {
      outline: none;
      box-shadow: 0px 0px 0px 2px rgba(176, 198, 225, 0.6);
    }
    &::after {
      content: "";
      position: absolute;
      top: 40%;
      right: 15px;
      transform: translateY(-40%) rotate(45deg);
      /* Рисуем треугольник */
      width: 0;
      height: 0;
      border: solid #333333;
      border-width: 0 2px 2px 0;
      display: inline-block;
      padding: 5px;
      /* Снимаем клик с треугольника */
      pointer-events: none;
    }
  }

  &__list {
    display: none;

    position: absolute;
    left: 0;
    top: 40px;

    margin: 0;
    padding: 0;
    list-style-type: none;

    background: #ffffff;
    box-shadow: 0px 2px 2px rgba(176, 198, 225, 0.6);
    overflow: hidden;

    border-radius: 6px;
    width: 315px;

    z-index: 1;
    &--visible {
      display: block;
    }
  }

  &__list-item {
    margin: 0;
    padding: 0;
    border: 1px solid #7e9bbd;
    border-bottom: 0px;
    padding: 8px;
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    cursor: pointer;
    &:first-child {
      border-radius: 6px 6px 0 0;
    }

    &:last-child {
      border-radius: 0 0 6px 6px;
      border-bottom: 1px solid #7e9bbd;
    }

    &:hover {
      background: rgba(176, 198, 225, 0.26);
    }
  }

  &__input-hidden {
    display: none;
  }
}
.product {
  &__size {
    display: block;
    margin-bottom: 40px;
    color: var(--brand-black, #333);
    font-size: 14px;
    font-family: Open Sans;
    font-style: normal;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: 0.56px;
    text-decoration-line: underline;
  }
}
.btns-prod {
  &__btn-minus {
    height: 44px;
    padding: 12px 20px;
    margin-right: 12px;
    margin-bottom: 5px;
    background-color: #f2f2f2;
  }

  &__amount {
    display: inline-block;
    height: 44px;
    margin-bottom: 5px;
    padding: 12px 20px;
    background-color: #f2f2f2;
    border-left: 1px solid #c4c4c4;
    border-right: 1px solid #c4c4c4;
  }

  &__btn-plus {
    height: 44px;
    margin-bottom: 5px;
    padding: 12px 20px;
    background-color: #f2f2f2;
  }

  &__btn-add {
    flex: 0 0 auto;
    height: 44px;
    margin-bottom: 5px;
    margin-right: 12px;
    padding: 8px 28px;
    border: 1px solid var(--brand-black, #333);
    background: var(--brand-black, #333);
    color: #fff;
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: 0.56px;
  }

  &__btn-like {
    margin-bottom: 5px;
    height: 44px;
    padding: 14px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid var(--brand-black, #333);
    background: var(--brand-black, #333);

    & svg {
      width: 16px;
      height: 16px;
      fill: none;
      stroke: #fff;
    }
  }
}

.graph-modal {
  &__container {
    max-width: 378px;
    border-radius: 0;
  }

  &__close {
    top: 24px;
    right: 24px;
    width: 11px;
    height: 11px;
  }

  &__content {
  }
}
.js-modal-close {
}
.content-modal {
  &__form {
    display: flex;
    flex-direction: column;
  }
}
.form {
  &__title {
    padding-top: 20px;
    padding-bottom: 40px;
  }
  &__label {
    margin: 0 -20px;
    padding-left: 20px;
    padding-right: 20px;
    display: flex;
    flex-direction: column;
    padding-bottom: 24px;
    border-bottom: 1px solid#E6E9F0;
  }

  &__input {
    padding: 22px 20px;
    margin-bottom: 16px;
    color: var(--unnamed, #303030);
    font-size: 16px;
    font-family: Montserrat;
    font-style: normal;
    font-weight: 300;
    line-height: normal;
    border: 0.5px solid var(--unnamed, #868686);
    background: #fdfcfc;
  }

  &__copy {
    padding-top: 40px;
    color: #303030;
    font-size: 10px;
    font-weight: 200;
    line-height: normal;
    letter-spacing: -0.1px;
  }

  &__link {
    padding-bottom: 33px;
    color: #303030;
    font-size: 10px;
    font-weight: 200;
    line-height: normal;
    letter-spacing: -0.1px;
  }

  &__btn {
    max-width: 135px;
    padding: 8px 28px;
    color: var(--brand-white, #fff);
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: 0.56px;
    border: 1px solid var(--brand-black, #333);
    background: var(--brand-black, #333);
  }
}

.list {
  margin: 0;
  padding: 24px 0px 0px 0px;
  list-style: none;
  &__items {
    &:not(:last-child) {
      padding-bottom: 12px;
    }
  }

  &__link {
    display: flex;
    align-items: center;
    color: var(--brand-black, #333);
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: 0.56px;
    text-decoration-line: underline;
    &:hover{
      color: #000;
      transition: color 0.3s ease-in;
    }
    &:hover .list__icon{
      stroke: #000;
      transition: stroke 0.3s ease-in;
    }
  }

  &__icon {
    margin-right: 4px;
    width: 20px;
    height: 20px;
    fill: none;
    stroke: #333333;
  }
}


@media all and (max-width: 700px){
  .product {
    &__slider {
      position: relative;
      display: flex;
      max-width: 475px;
    }
}
.slider-nav {
  &__item {
    width: 60px;
    height: 72px;
    &:not(:last-child) {
      margin-bottom: 4px;
    }
  }
}
}
@media all and (max-width: 500px){
  .product {
    &__container {
      padding-bottom: 45px;
    }
    &__slider {
      position: relative;
      display: flex;
      max-width: 375px;
    }
}
.slider-nav {
  &__item {
    width: 40px;
    height: 52px;
    &:not(:last-child) {
      margin-bottom: 4px;
    }
  }
}
}

@media all and (max-width: 390px){
  .product {
    &__container {
      padding-bottom: 45px;
    }
    &__slider {
      position: relative;
      display: flex;
      max-width: 320px;
    }
}
.slider-nav {
  &__item {
    width: 35px;
    height: 48px;
    &:not(:last-child) {
      margin-bottom: 4px;
    }
  }
}
.dropdown__button{
  width: 250px;
}
}
