.header {
  box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.1);
  margin-bottom: 22px;
  &__container {
    padding: 8px 26px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #fff;
  }

  &__logo {
    color: #000;
    font-size: 22px;
    font-family: Open Sans;
    font-style: normal;
    font-weight: 700;
    line-height: 16px;
    letter-spacing: 0.88px;
    text-transform: uppercase;
  }

  &__items {
    padding: 0;
    list-style: none;
    display: flex;
    justify-content: space-between;
  }
}
.items-header {

  &_item {
    &:not(:last-child) {
      margin-right: 24px;
    }
  }
  &__icon {
    width: 20px;
    height: 20px;
    fill: #fff;
    stroke: #333333;
    &:hover {
      stroke: #000;
    }
  }
}

.burger {
  @include burger;
  z-index: 4;
  display: none;
}
.menu {
  padding: 80px 0px 30px 30px;
  position: absolute;
  top: -1000px;
  left: 0px;
  right: 0px;
  z-index: 3;
  list-style: none;
  background: #fff;
  box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.1);
  transition: 0.4s ease-in;
  &--active {
    top: 0px;
    transition: 0.4s ease-in;
  }

  &__item {
    padding-bottom: 30px;
  }

  &__link {
    color: var(--brand-black);
    font-size: 12px;
    font-family: Open Sans;
    font-style: normal;
    font-weight: 400;
    line-height: 16px;
    letter-spacing: 0.48px;
    text-transform: uppercase;
  }
}
@media all and (max-width: 375px){
  .burger{
    display: inline-block;
}
}
