.footer {
  &__container {
    padding: 0px 117px 26px 117px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  &__byuers {
    margin: 0px 20px 0px 0px;
    padding: 12px 0px 20px 0px;
    list-style: none;
  }

  &__about {
    margin: 0px 20px 0px 0px;
    padding: 12px 0px 20px 0px;
    list-style: none;
  }

  &__subs {
    display: flex;
    flex-direction: column;
  }
}

.about {
  &__title {
  }

  &__item {
    padding-bottom: 8px;
  }

  &__link {
    color: var(--brand-black);
    font-size: 14px;
    font-family: Open Sans;
    font-style: normal;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: 0.56px;
    transition: 0.2s ease-in-out;
    &:hover {
      color: #000;
      transition: 0.2s ease-in-out;
    }
  }
}
.byuers {
  &__item {
    padding-bottom: 8px;
  }

  &__link {
    color: var(--brand-black);
    font-size: 14px;
    font-family: Open Sans;
    font-style: normal;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: 0.56px;
    transition: 0.2s ease-in-out;
    &:hover {
      color: #000;
      transition: 0.2s ease-in-out;
    }
  }
}
.subs {
  &__title {
    padding: 12px 0px 12px 0px;
    margin-bottom: 17px;
  }

  &__form {
    display: flex;
    flex-direction: column;
  }
}
.form-subs {
  &__label {
    position: relative;
    margin-bottom: 35px;
    border-bottom: 1px solid #c4c4c4;
  }

  &__input {
    padding: 8px 30px 8px 12px;
    width: 100%;
    color: var(--brand-grey-dark);
    font-size: 14px;
    font-family: Open Sans;
    font-style: normal;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: 0.56px;
  }

  &__btn-res {
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    & svg {
      width: 16px;
      height: 16px;
      stroke: #c4c4c4;
    }
  }

  &__btn {
    align-self: end;
    padding: 8px 28px;
    max-width: 150px;
    border: 1px solid var(--brand-black, #333);
    background: var(--brand-white, #fff);
    color: var(--brand-black);
    font-size: 14px;
    font-family: Open Sans;
    font-style: normal;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: 0.56px;
  }
}


@media all and (max-width: 500px){
  .footer {
    &__container {
      padding: 0px 20px 26px 20px;
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
    }
}
}
